.PokemonList {
  background-color: #282c34;
  height: 90%;
  overflow: hidden;
  overflow-y: scroll;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Pokemon:hover {
  background-color: rgb(98, 0, 255);
  cursor: pointer;
}

ol {
  margin: 0 0 0 50px;
}



